und-color: green;\n}\n\n.hero {\n    display: flex;\n    align-items: center;\n    min-height: 380px;\n    width: 100%;\n    text-align: center;\n    background-image: url('../public/heros/hero-image_4.jpg');\n}\n\n.hero-content {\n    margin: 0 auto;\n    max-width: 1000px\n}\n\n.hero-title {\n    color: #fff;\n    font-weight: 600;\n    font-size: 36px;\n    text-shadow: 1px 1px #141414\n}\n\n.hero-subtitle {\n    color: #fff;\n    margin-top: 16px;\n    font-size: 20px;\n    font-weight: 650;\n    text-shadow: 1px 1px #141414\n}\n\n.hero__inner {\n    color: white;\n}\n\n\n/* Restaurant Item */\n\n.main-title {\n    padding: 20px;\n    text-align: center;\n}\n\n.restaurant-list {\n    display: grid;\n    grid-template-columns: 1fr;\n    margin: 20px;\n    justify-items: center;\n}\n\n.restaurant-item {\n    display: grid;\n    margin: 0 10px 18px 10px;\n    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\n    border-radius: 10px;\n    overflow: hidden;\n    max-width: 400px;\n}\n\n.resto-img {\n    position: relative;\n    width: 100%;\n    max-height: 180px;\n    object-fit: cover;\n}\n\n.fan-art-restaurant {\n    width: 100%;\n    max-height: 300px;\n    object-fit: cover;\n    object-position: center;\n}\n\n.restaurant-city {\n    position: absolute;\n    margin-top: 10px;\n    background: rgba(255, 255, 255, 0.8);\n    padding: 5px 10px 5px 30px;\n    border-radius: 0 2px 2px 0;\n}\n\n.restaurant-info {\n    padding: 24px;\n    padding-top: 0px;\n}\n\n.restaurant-info a {\n    text-decoration: none;\n    color: black;\n    padding: 10px;\n}\n\n.restaurant-info a:hover {\n    color: blue;\n}\n\n.restaurant-info>h2 {\n    font-weight: lighter;\n}\n\n.restaurant-info>p {\n    margin-top: 10px;\n    overflow: hidden;\n    text-overflow: ellipsis;\n    display: -webkit-box;\n    -webkit-box-orient: vertical;\n    -webkit-line-clamp: 4;\n    /* number of lines to show */\n}\n\n.rating {\n    margin-bottom: 20px;\n    font-weight: bold;\n}\n\n\n/* Restaurant Detail */\n\n.restaurant-detail {\n    display: grid;\n    margin: 50px 120px;\n    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;\n}\n\n.restaurant-thumbnail {\n    grid-column-start: 1;\n    grid-column-end: 4;\n}\n\n.restaurant__name {\n    margin-bottom: 20px;\n}\n\n.restaurant__description {\n    margin-top: 20px;\n    grid-column-start: 1;\n    grid-column-end: 7;\n}\n\n.restaurant__description>h3 {\n    margin-bottom: 10px;\n}\n\n.customer-review {\n    grid-column-start: 1;\n    grid-column-end: 7;\n    text-align: center;\n    padding: 20px 0;\n}\n\n.review {\n    display: grid;\n    grid-template-columns: 1fr 1fr 1fr;\n}\n\n.review-detail {\n    margin: 10px 20px;\n    padding: 20px;\n    border-radius: 10px;\n    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\n    grid-column: span 2;\n}\n\n.review-main {\n    display: grid;\n    grid-template-columns: 1fr 7fr;\n    margin-bottom: 15px;\n}\n\n.review-main>i {\n    display: grid;\n    grid-row-start: 1;\n    grid-row-end: 3;\n}\n\n.review-main>p {\n    grid-column-start: 2;\n    grid-column-end: 3;\n}\n\n.restaurant__info {\n    margin-left: 20px;\n    grid-column-start: 4;\n    grid-column-end: 7;\n}\n\n.restaurant__info>p {\n    margin-top: 10px;\n}\n\n.restaurant__info>h3 {\n    margin-top: 10px;\n}\n\n.restaurant__poster {\n    width: 100%;\n    height: 100%;\n    border-radius: 10px;\n    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\n    object-fit: cover;\n}\n\n.like {\n    width: 55px;\n    height: 55px;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    background-color: #db0000;\n    position: fixed;\n    bottom: 16px;\n    right: 16px;\n    border-radius: 50%;\n    border: 0;\n    font-size: 18px;\n    color: white;\n    cursor: pointer;\n}\n\nfooter {\n    background-color: #FFF8EA;\n    color: rgb(40, 34, 34);\n    font-size: 18px;\n    padding: 2em;\n    width: 100%;\n    text-align: center;\n}\n\nfooter>p {\n    margin: 16px;\n}\n\n#loading {\n    width: 2rem;\n    height: 2re